import Script from 'next/script';
import { useEffect } from 'react';

const TelegramLogin = () => {
    useEffect(() => {
        // Проверим, что скрипт был загружен
        if (window.Telegram) {
            // Инициализация Telegram кнопки
            window.Telegram.loginWidget({
                bot_username: "neurotinniusbot",
                button_size: "large",
                request_access: "write",
                onauth: function(user) {
                    alert('Logged in as ' + user.first_name + ' ' + user.last_name + ' (' + user.id + (user.username ? ', @' + user.username : '') + ')');
                }
            });
        }
    }, []);

    return (
        <div>
            {/* Загрузка скрипта Telegram */}
            <Script
                strategy="afterInteractive" // Скрипт будет загружен после рендеринга
                src="https://telegram.org/js/telegram-widget.js?22"
                data-telegram-login="neurotinniusbot"
                data-size="large"
                data-onauth="onTelegramAuth(user)"
                data-request-access="write"
            />
        </div>
    );
};

export default TelegramLogin;
